/**
 * Ââ™Ë¥¥ÊùøÂ∑•ÂÖ∑Á±ª
 * Â∞ÅË£ÖÂâ™Ë¥¥ÊùøÊìç‰ΩúÔºåÊîØÊåÅÊñáÊú¨ÂíåÂõæÁâáÂ§çÂà∂
 */
export class ClipboardUtil {
    /**
     * Â§çÂà∂ÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
     * @param {string} text - Ë¶ÅÂ§çÂà∂ÁöÑÊñáÊú¨
     * @returns {Promise<boolean>} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static async copyText(text) {
        try {
            // ÂèÇÊï∞È™åËØÅ
            if (typeof text !== 'string') {
                throw new Error('Â§çÂà∂ÁöÑÂÜÖÂÆπÂøÖÈ°ªÊòØÂ≠óÁ¨¶‰∏≤')
            }

            if (text.trim() === '') {
                throw new Error('Â§çÂà∂ÁöÑÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫')
            }

            // ‰ºòÂÖà‰ΩøÁî®Áé∞‰ª£ Clipboard API
            if (navigator.clipboard && window.isSecureContext) {
                await navigator.clipboard.writeText(text)
                return true
            } else {
                // ÂÖºÂÆπÊóßÊµèËßàÂô®ÁöÑÊñπÊ°à
                return this.fallbackCopyText(text)
            }
        } catch (error) {
            console.error('Â§çÂà∂ÊñáÊú¨Â§±Ë¥•:', error)
            return false
        }
    }

    /**
     * ÂÖºÂÆπÊóßÊµèËßàÂô®ÁöÑÊñáÊú¨Â§çÂà∂ÊñπÊ≥ï
     * @param {string} text - Ë¶ÅÂ§çÂà∂ÁöÑÊñáÊú¨
     * @returns {boolean} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static fallbackCopyText(text) {
        let input = null
        try {
            // ÂàõÂª∫‰∏¥Êó∂ËæìÂÖ•Ê°Ü
            input = document.createElement('textarea')
            input.value = text

            // ËÆæÁΩÆÊ†∑ÂºèÔºå‰ΩøÂÖ∂‰∏çÂèØËßÅ‰ΩÜÂèØÈÄâ‰∏≠
            input.style.position = 'fixed'
            input.style.left = '-9999px'
            input.style.top = '-9999px'
            input.style.opacity = '0'
            input.style.pointerEvents = 'none'

            document.body.appendChild(input)

            // ÈÄâ‰∏≠ÊñáÊú¨
            input.focus()
            input.select()
            input.setSelectionRange(0, text.length)

            // ÊâßË°åÂ§çÂà∂ÂëΩ‰ª§
            const success = document.execCommand('copy')
            return success
        } catch (error) {
            console.error('ÂÖºÂÆπÊ®°ÂºèÂ§çÂà∂Â§±Ë¥•:', error)
            return false
        } finally {
            // Ê∏ÖÁêÜ‰∏¥Êó∂ÂÖÉÁ¥†
            if (input && input.parentNode) {
                document.body.removeChild(input)
            }
        }
    }

    /**
     * Â§çÂà∂ÂõæÁâáÂà∞Ââ™Ë¥¥Êùø
     * @param {string|HTMLCanvasElement|Blob} source - ÂõæÁâáÊ∫ê
     * @returns {Promise<boolean>} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static async copyImage(source) {
        try {
            if (!navigator.clipboard || !navigator.clipboard.write) {
                throw new Error('ÊµèËßàÂô®‰∏çÊîØÊåÅÂõæÁâáÂ§çÂà∂ÂäüËÉΩ')
            }

            let blob

            if (source instanceof Blob) {
                blob = source
            } else if (source instanceof HTMLCanvasElement) {
                blob = await this.canvasToBlob(source)
            } else if (typeof source === 'string') {
                // ÂÅáËÆæÊòØDataURLÊàñURL
                blob = await this.dataURLToBlob(source)
            } else {
                throw new Error('‰∏çÊîØÊåÅÁöÑÂõæÁâáÊ∫êÁ±ªÂûã')
            }

            const item = new ClipboardItem({ [blob.type]: blob })
            await navigator.clipboard.write([item])
            return true
        } catch (error) {
            console.error('Â§çÂà∂ÂõæÁâáÂ§±Ë¥•:', error)
            return false
        }
    }

    /**
     * CanvasËΩ¨Blob
     * @param {HTMLCanvasElement} canvas - CanvasÂÖÉÁ¥†
     * @param {string} type - ÂõæÁâáÁ±ªÂûã
     * @param {number} quality - ÂõæÁâáË¥®Èáè
     * @returns {Promise<Blob>} BlobÂØπË±°
     */
    static canvasToBlob(canvas, type = 'image/png', quality = 1.0) {
        return new Promise((resolve) => {
            canvas.toBlob(resolve, type, quality)
        })
    }

    /**
     * DataURLËΩ¨Blob
     * @param {string} dataURL - DataURLÂ≠óÁ¨¶‰∏≤
     * @returns {Promise<Blob>} BlobÂØπË±°
     */
    static async dataURLToBlob(dataURL) {
        try {
            const response = await fetch(dataURL)
            return await response.blob()
        } catch (error) {
            console.error('DataURLËΩ¨BlobÂ§±Ë¥•:', error)
            throw error
        }
    }

    /**
     * ËØªÂèñÂâ™Ë¥¥ÊùøÊñáÊú¨
     * @returns {Promise<string>} Ââ™Ë¥¥ÊùøÊñáÊú¨ÂÜÖÂÆπ
     */
    static async readText() {
        try {
            if (navigator.clipboard && navigator.clipboard.readText) {
                return await navigator.clipboard.readText()
            } else {
                throw new Error('ÊµèËßàÂô®‰∏çÊîØÊåÅËØªÂèñÂâ™Ë¥¥Êùø')
            }
        } catch (error) {
            console.error('ËØªÂèñÂâ™Ë¥¥ÊùøÂ§±Ë¥•:', error)
            throw error
        }
    }

    /**
     * ËØªÂèñÂâ™Ë¥¥ÊùøÂõæÁâá
     * @returns {Promise<Blob>} Ââ™Ë¥¥ÊùøÂõæÁâáBlob
     */
    static async readImage() {
        try {
            if (!navigator.clipboard || !navigator.clipboard.read) {
                throw new Error('ÊµèËßàÂô®‰∏çÊîØÊåÅËØªÂèñÂâ™Ë¥¥ÊùøÂõæÁâá')
            }

            const clipboardItems = await navigator.clipboard.read()
            
            for (const clipboardItem of clipboardItems) {
                for (const type of clipboardItem.types) {
                    if (type.startsWith('image/')) {
                        return await clipboardItem.getType(type)
                    }
                }
            }
            
            throw new Error('Ââ™Ë¥¥Êùø‰∏≠Ê≤°ÊúâÂõæÁâá')
        } catch (error) {
            console.error('ËØªÂèñÂâ™Ë¥¥ÊùøÂõæÁâáÂ§±Ë¥•:', error)
            throw error
        }
    }

    /**
     * Ê£ÄÊü•Ââ™Ë¥¥ÊùøAPIÊîØÊåÅÊÉÖÂÜµ
     * @returns {Object} ÊîØÊåÅÊÉÖÂÜµÂØπË±°
     */
    static checkSupport() {
        return {
            writeText: !!(navigator.clipboard && navigator.clipboard.writeText),
            readText: !!(navigator.clipboard && navigator.clipboard.readText),
            writeImage: !!(navigator.clipboard && navigator.clipboard.write),
            readImage: !!(navigator.clipboard && navigator.clipboard.read),
            isSecureContext: window.isSecureContext
        }
    }

    /**
     * Â§çÂà∂Â§öË°åÊñáÊú¨ÔºàÊï∞ÁªÑËΩ¨Êç¢‰∏∫Êç¢Ë°åÂàÜÈöîÁöÑÂ≠óÁ¨¶‰∏≤Ôºâ
     * @param {Array} lines - ÊñáÊú¨Ë°åÊï∞ÁªÑ
     * @returns {Promise<boolean>} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static async copyLines(lines) {
        const text = Array.isArray(lines) ? lines.join('\n') : String(lines)
        return await this.copyText(text)
    }

    /**
     * Â§çÂà∂ÂØπË±°‰∏∫JSONÊ†ºÂºè
     * @param {Object} obj - Ë¶ÅÂ§çÂà∂ÁöÑÂØπË±°
     * @param {number} indent - Áº©ËøõÁ©∫Ê†ºÊï∞
     * @returns {Promise<boolean>} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static async copyJSON(obj, indent = 2) {
        try {
            const jsonText = JSON.stringify(obj, null, indent)
            return await this.copyText(jsonText)
        } catch (error) {
            console.error('Â§çÂà∂JSONÂ§±Ë¥•:', error)
            return false
        }
    }

    /**
     * Â§çÂà∂Ë°®Ê†ºÊï∞ÊçÆ‰∏∫Âà∂Ë°®Á¨¶ÂàÜÈöîÁöÑÊñáÊú¨
     * @param {Array} data - Ë°®Ê†ºÊï∞ÊçÆÊï∞ÁªÑ
     * @param {Array} headers - Ë°®Â§¥Êï∞ÁªÑÔºàÂèØÈÄâÔºâ
     * @returns {Promise<boolean>} Â§çÂà∂ÊòØÂê¶ÊàêÂäü
     */
    static async copyTable(data, headers = null) {
        try {
            let text = ''
            
            // Ê∑ªÂä†Ë°®Â§¥
            if (headers && headers.length > 0) {
                text += headers.join('\t') + '\n'
            }
            
            // Ê∑ªÂä†Êï∞ÊçÆË°å
            data.forEach(row => {
                if (Array.isArray(row)) {
                    text += row.join('\t') + '\n'
                } else if (typeof row === 'object') {
                    const values = headers ? headers.map(header => row[header] || '') : Object.values(row)
                    text += values.join('\t') + '\n'
                }
            })
            
            return await this.copyText(text.trim())
        } catch (error) {
            console.error('Â§çÂà∂Ë°®Ê†ºÂ§±Ë¥•:', error)
            return false
        }
    }
}

export default ClipboardUtil
