          <div class="color-mode-tabs">
            <el-tabs v-model="colorMode" @tab-click="generatePreview">
              <el-tab-pane label="Á∫ØËâ≤" name="solid">
                <div class="color-picker-group">
                  <div class="color-item">
                    <label>ÊñáÂ≠óÈ¢úËâ≤:</label>
                    <el-color-picker v-model="textColor" @change="generatePreview"></el-color-picker>
                  </div>
                </div>
              </el-tab-pane>
              <el-tab-pane label="Ê∏êÂèòËâ≤" name="gradient">
                <div class="gradient-settings">
                  <div class="color-picker-group">
