                      <el-color-picker v-model="gradientStart" @change="generatePreview"></el-color-picker>
                    </div>
                    <div class="color-item">
                      <label>ÁªìÊùüÈ¢úËâ≤:</label>
                      <el-color-picker v-model="gradientEnd" @change="generatePreview"></el-color-picker>
                    </div>
                  </div>
                  <div class="gradient-direction">
                    <label>Ê∏êÂèòÊñπÂêë:</label>
                    <el-select v-model="gradientDirection" @change="generatePreview">
                      <el-option label="‰ªéÂ∑¶Âà∞Âè≥" value="to right"></el-option>
                      <el-option label="‰ªé‰∏äÂà∞‰∏ã" value="to bottom"></el-option>
                      <el-option label="‰ªéÂ∑¶‰∏äÂà∞Âè≥‰∏ã" value="to bottom right"></el-option>
                      <el-option label="‰ªéÂè≥‰∏äÂà∞Â∑¶‰∏ã" value="to bottom left"></el-option>
                      <el-option label="ÂæÑÂêëÊ∏êÂèò" value="radial"></el-option>
                    </el-select>
                  </div>
