              <el-slider 
                v-model="padding" 
                :min="0" 
                :max="100" 
                @change="generatePreview"
                style="flex: 1; margin: 0 15px;"
              ></el-slider>
              <span class="value-display">{{ padding }}px</span>
            </div>
          </div>

          <div class="preset-sizes">
            <label>È¢ÑËÆæÂ∞∫ÂØ∏:</label>
            <el-button-group>
              <el-button size="small" @click="setPresetSize(800, 600)">4:3</el-button>
              <el-button size="small" @click="setPresetSize(1920, 1080)">16:9</el-button>
              <el-button size="small" @click="setPresetSize(1080, 1080)">1:1</el-button>
              <el-button size="small" @click="setPresetSize(1080, 1920)">9:16</el-button>
            </el-button-group>
          </div>
