        <h3>üëÄ ÂÆûÊó∂È¢ÑËßà</h3>
        <div class="preview-container">
          <canvas 
            ref="previewCanvas" 
            class="preview-canvas"
            :style="{ maxWidth: '100%', maxHeight: '400px' }"
          ></canvas>
        </div>

        <div class="preview-info">
          <div class="info-item">
            <span class="info-label">Â∞∫ÂØ∏:</span>
            <span class="info-value">{{ canvasWidth }} √ó {{ canvasHeight }}px</span>
          </div>
          <div class="info-item">
            <span class="info-label">Â≠óÁ¨¶Êï∞:</span>
            <span class="info-value">{{ textContent.length }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">Ë°åÊï∞:</span>
            <span class="info-value">{{ textLines.length }}</span>
          </div>
        </div>

        <div class="action-buttons">
          <el-button type="primary" size="large" @click="downloadImage">
            <i class="el-icon-download"></i> ‰∏ãËΩΩPNG
          </el-button>
          <el-button size="large" @click="downloadJPG">
            <i class="el-icon-download"></i> ‰∏ãËΩΩJPG
          </el-button>
          <el-button size="large" @click="copyToClipboard">
            <i class="el-icon-copy-document"></i> Â§çÂà∂ÂõæÁâá
          </el-button>
        </div>
      </div>
    </div>

    <!-- Ê®°ÊùøÂå∫Âüü -->
    <div class="templates-section">
      <h3>üîñ Âø´ÈÄüÊ®°Êùø</h3>
      <div class="template-grid">
        <div 
          v-for="template in templates" 
          :key="template.name"
          class="template-item"
          @click="applyTemplate(template)"
        >
          <div class="template-preview" :style="template.previewStyle">
            {{ template.name }}
          </div>
          <div class="template-name">{{ template.name }}</div>
