</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'TextImageGeneratorView',
  data() {
    return {
      textContent: 'Âú®ËøôÈáåËæìÂÖ•‰Ω†ÁöÑÊñáÂ≠ó\nÊîØÊåÅÂ§öË°åÊñáÊú¨',
      
      // ÊñáÂ≠óÊ†∑Âºè
      fontSize: 48,
      fontFamily: 'Microsoft YaHei, sans-serif',
      textAlign: 'center',
      fontBold: false,
      fontItalic: false,
      textShadow: false,
      
      // È¢úËâ≤ËÆæÁΩÆ
      colorMode: 'solid',
      textColor: '#2c3e50',
      gradientStart: '#667eea',
      gradientEnd: '#764ba2',
      gradientDirection: 'to right',
      
      // ÁîªÂ∏ÉËÆæÁΩÆ
      canvasWidth: 800,
      canvasHeight: 600,
      backgroundColor: '#ffffff',
      padding: 40,
      
      // Ê®°Êùø
      templates: [
        {
          name: 'ÁªèÂÖ∏ÈªëÁôΩ',
          textContent: 'ÁªèÂÖ∏ÈªëÁôΩÈ£éÊ†º',
          textColor: '#000000',
          backgroundColor: '#ffffff',
          fontSize: 48,
          fontFamily: 'Microsoft YaHei, sans-serif',
          previewStyle: { background: '#ffffff', color: '#000000' }
        },
        {
          name: 'Ê∏êÂèòËìùÁ¥´',
          textContent: 'Ê∏êÂèòËìùÁ¥´È£éÊ†º',
          colorMode: 'gradient',
          gradientStart: '#667eea',
          gradientEnd: '#764ba2',
          backgroundColor: '#f8f9fa',
          fontSize: 52,
          fontBold: true,
          previewStyle: { background: 'linear-gradient(to right, #667eea, #764ba2)', color: 'transparent', backgroundClip: 'text', WebkitBackgroundClip: 'text' }
        },
        {
          name: 'Ê∏©ÊöñÊ©ôËâ≤',
          textContent: 'Ê∏©ÊöñÊ©ôËâ≤È£éÊ†º',
          textColor: '#ff6b35',
          backgroundColor: '#fff8f0',
          fontSize: 46,
          fontFamily: 'SimHei, sans-serif',
          previewStyle: { background: '#fff8f0', color: '#ff6b35' }
        },
        {
          name: 'Ê∏ÖÊñ∞ÁªøËâ≤',
          textContent: 'Ê∏ÖÊñ∞ÁªøËâ≤È£éÊ†º',
          textColor: '#27ae60',
          backgroundColor: '#f0fff4',
          fontSize: 44,
          textShadow: true,
          previewStyle: { background: '#f0fff4', color: '#27ae60', textShadow: '2px 2px 4px rgba(0,0,0,0.3)' }
        }
      ]
    }
  },
  computed: {
    textLines() {
      return this.textContent.split('\n').filter(line => line.trim())
    }
  },
  mounted() {
    this.generatePreview()
  },
  methods: {
    generatePreview() {
      this.$nextTick(() => {
        const canvas = this.$refs.previewCanvas
        if (!canvas) return
        
        const ctx = canvas.getContext('2d')
        
        // ËÆæÁΩÆÁîªÂ∏ÉÂ∞∫ÂØ∏
        canvas.width = this.canvasWidth
        canvas.height = this.canvasHeight
        
        // Ê∏ÖÁ©∫ÁîªÂ∏É
        ctx.clearRect(0, 0, canvas.width, canvas.height)
        
        // ÁªòÂà∂ËÉåÊôØ
        ctx.fillStyle = this.backgroundColor
        ctx.fillRect(0, 0, canvas.width, canvas.height)
        
        // ËÆæÁΩÆÊñáÂ≠óÊ†∑Âºè
        const fontWeight = this.fontBold ? 'bold' : 'normal'
        const fontStyle = this.fontItalic ? 'italic' : 'normal'
        ctx.font = `${fontStyle} ${fontWeight} ${this.fontSize}px ${this.fontFamily}`
        
        // ËÆæÁΩÆÊñáÂ≠óÈ¢úËâ≤
        if (this.colorMode === 'solid') {
          ctx.fillStyle = this.textColor
        } else {
          // ÂàõÂª∫Ê∏êÂèò
          let gradient
          if (this.gradientDirection === 'radial') {
            const centerX = canvas.width / 2
            const centerY = canvas.height / 2
            const radius = Math.min(canvas.width, canvas.height) / 2
            gradient = ctx.createRadialGradient(centerX, centerY, 0, centerX, centerY, radius)
          } else {
            const directions = {
              'to right': [0, 0, canvas.width, 0],
              'to bottom': [0, 0, 0, canvas.height],
              'to bottom right': [0, 0, canvas.width, canvas.height],
              'to bottom left': [canvas.width, 0, 0, canvas.height]
            }
            const [x0, y0, x1, y1] = directions[this.gradientDirection] || directions['to right']
            gradient = ctx.createLinearGradient(x0, y0, x1, y1)
          }
          
          gradient.addColorStop(0, this.gradientStart)
          gradient.addColorStop(1, this.gradientEnd)
          ctx.fillStyle = gradient
        }
        
        // ËÆæÁΩÆÈò¥ÂΩ±
        if (this.textShadow) {
          ctx.shadowColor = 'rgba(0, 0, 0, 0.5)'
          ctx.shadowBlur = 4
          ctx.shadowOffsetX = 2
          ctx.shadowOffsetY = 2
        } else {
          ctx.shadowColor = 'transparent'
          ctx.shadowBlur = 0
          ctx.shadowOffsetX = 0
          ctx.shadowOffsetY = 0
        }
        
        // ÁªòÂà∂ÊñáÂ≠ó
        this.drawText(ctx)
      })
    },
    
    drawText(ctx) {
      const lines = this.textLines
      if (lines.length === 0) return
      
      const lineHeight = this.fontSize * 1.2
      const totalHeight = lines.length * lineHeight
      const startY = (this.canvasHeight - totalHeight) / 2 + this.fontSize
      
      lines.forEach((line, index) => {
        const y = startY + index * lineHeight + this.padding
        let x
        
        switch (this.textAlign) {
          case 'left':
            ctx.textAlign = 'left'
            x = this.padding
            break
          case 'right':
            ctx.textAlign = 'right'
            x = this.canvasWidth - this.padding
            break
          default:
            ctx.textAlign = 'center'
            x = this.canvasWidth / 2
        }
        
        ctx.fillText(line, x, y)
      })
    },
    
    setPresetSize(width, height) {
      this.canvasWidth = width
      this.canvasHeight = height
      this.generatePreview()
    },
    
    downloadImage() {
      const canvas = this.$refs.previewCanvas
      const filename = DownloadUtil.generateTimestampFilename('text-image', 'png')
      const dataURL = canvas.toDataURL('image/png')
      DownloadUtil.downloadImage(dataURL, filename)
      this.$message.success('PNGÂõæÁâáÂ∑≤‰∏ãËΩΩÔºÅ')
    },
    
    downloadJPG() {
      const canvas = this.$refs.previewCanvas
      const filename = DownloadUtil.generateTimestampFilename('text-image', 'jpg')
      const dataURL = canvas.toDataURL('image/jpeg', 0.9)
      DownloadUtil.downloadImage(dataURL, filename)
      this.$message.success('JPGÂõæÁâáÂ∑≤‰∏ãËΩΩÔºÅ')
    },
    
    async copyToClipboard() {
      try {
        const canvas = this.$refs.previewCanvas
        const success = await ClipboardUtil.copyCanvas(canvas)
        if (success) {
          this.$message.success('ÂõæÁâáÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÔºÅ')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•ÔºåËØ∑‰ΩøÁî®‰∏ãËΩΩÂäüËÉΩÔºÅ')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•ÔºåËØ∑‰ΩøÁî®‰∏ãËΩΩÂäüËÉΩÔºÅ')
      }
    },
    
    applyTemplate(template) {
      // Â∫îÁî®Ê®°ÊùøËÆæÁΩÆ
      Object.keys(template).forEach(key => {
        if (key !== 'name' && key !== 'previewStyle' && Object.prototype.hasOwnProperty.call(this, key)) {
          this[key] = template[key]
        }
      })
      
      this.generatePreview()
      this.$message.success(`Â∑≤Â∫îÁî®Ê®°ÊùøÔºö${template.name}`)
    }
  }
}
</script>

