/**
 * ‰∏ãËΩΩÂ∑•ÂÖ∑Á±ª
 * Â∞ÅË£ÖÂêÑÁßç‰∏ãËΩΩÂäüËÉΩÔºåÈÅøÂÖçÈáçÂ§ç‰ª£Á†Å
 */
export class DownloadUtil {
    /**
     * ‰∏ãËΩΩÊñáÊú¨Êñá‰ª∂
     * @param {string} content - Êñá‰ª∂ÂÜÖÂÆπ
     * @param {string} filename - Êñá‰ª∂Âêç
     * @param {string} mimeType - MIMEÁ±ªÂûã
     */
    static downloadText(content, filename, mimeType = 'text/plain') {
        const blob = new Blob([content], { type: mimeType })
        this.downloadBlob(blob, filename)
    }

    /**
     * ‰∏ãËΩΩJSONÊñá‰ª∂
     * @param {Object} data - JSONÊï∞ÊçÆ
     * @param {string} filename - Êñá‰ª∂Âêç
     */
    static downloadJSON(data, filename = 'data.json') {
        const content = JSON.stringify(data, null, 2)
        this.downloadText(content, filename, 'application/json')
    }

    /**
     * ‰∏ãËΩΩCSVÊñá‰ª∂
     * @param {Array} data - CSVÊï∞ÊçÆÊï∞ÁªÑ
     * @param {string} filename - Êñá‰ª∂Âêç
     * @param {Array} headers - Ë°®Â§¥Êï∞ÁªÑ
     */
    static downloadCSV(data, filename = 'data.csv', headers = null) {
        let csvContent = ''
        
        // Ê∑ªÂä†Ë°®Â§¥
        if (headers && headers.length > 0) {
            csvContent += headers.join(',') + '\n'
        }
        
        // Ê∑ªÂä†Êï∞ÊçÆË°å
        data.forEach(row => {
            if (Array.isArray(row)) {
                csvContent += row.map(field => `"${field}"`).join(',') + '\n'
            } else if (typeof row === 'object') {
                const values = headers ? headers.map(header => row[header] || '') : Object.values(row)
                csvContent += values.map(field => `"${field}"`).join(',') + '\n'
            }
        })
        
        // Ê∑ªÂä†BOM‰ª•ÊîØÊåÅ‰∏≠Êñá
        const BOM = '\uFEFF'
        this.downloadText(BOM + csvContent, filename, 'text/csv;charset=utf-8')
    }

    /**
     * ‰∏ãËΩΩÂõæÁâáÊñá‰ª∂
     * @param {string} dataURL - ÂõæÁâáDataURL
     * @param {string} filename - Êñá‰ª∂Âêç
     */
    static downloadImage(dataURL, filename = 'image.png') {
        const link = document.createElement('a')
        link.download = filename
        link.href = dataURL
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * ‰∏ãËΩΩCanvas‰∏∫ÂõæÁâá
     * @param {HTMLCanvasElement} canvas - CanvasÂÖÉÁ¥†
     * @param {string} filename - Êñá‰ª∂Âêç
     * @param {string} format - ÂõæÁâáÊ†ºÂºè
     * @param {number} quality - ÂõæÁâáË¥®Èáè(0-1)
     */
    static downloadCanvas(canvas, filename = 'canvas.png', format = 'image/png', quality = 1.0) {
        const dataURL = canvas.toDataURL(format, quality)
        this.downloadImage(dataURL, filename)
    }

    /**
     * ‰∏ãËΩΩBlobÂØπË±°
     * @param {Blob} blob - BlobÂØπË±°
     * @param {string} filename - Êñá‰ª∂Âêç
     */
    static downloadBlob(blob, filename) {
        const url = URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
    }

    /**
     * ‰∏ãËΩΩURLÊåáÂêëÁöÑÊñá‰ª∂
     * @param {string} url - Êñá‰ª∂URL
     * @param {string} filename - Êñá‰ª∂Âêç
     */
    static downloadURL(url, filename) {
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        link.target = '_blank'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * ÊâπÈáè‰∏ãËΩΩÊñá‰ª∂‰∏∫ZIP
     * @param {Array} files - Êñá‰ª∂Êï∞ÁªÑ [{name: string, content: string|Blob}]
     * @param {string} zipName - ZIPÊñá‰ª∂Âêç
     */
    static async downloadZip(files, zipName = 'files.zip') {
        // Âä®ÊÄÅÂØºÂÖ•JSZip
        const JSZip = (await import('jszip')).default
        const zip = new JSZip()

        // Ê∑ªÂä†Êñá‰ª∂Âà∞ZIP
        files.forEach(file => {
            if (file.content instanceof Blob) {
                zip.file(file.name, file.content)
            } else {
                zip.file(file.name, file.content)
            }
        })

        // ÁîüÊàêZIPÂπ∂‰∏ãËΩΩ
        try {
            const content = await zip.generateAsync({ type: 'blob' })
            this.downloadBlob(content, zipName)
            return true
        } catch (error) {
            console.error('ÁîüÊàêZIPÊñá‰ª∂Â§±Ë¥•:', error)
            return false
        }
    }

    /**
     * ‰ªéURLËé∑ÂèñBlob
     * @param {string} url - Êñá‰ª∂URL
     * @returns {Promise<Blob>} BlobÂØπË±°
     */
    static async fetchBlob(url) {
        try {
            const response = await fetch(url)
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`)
            }
            return await response.blob()
        } catch (error) {
            console.error('Ëé∑ÂèñÊñá‰ª∂Â§±Ë¥•:', error)
            throw error
        }
    }

    /**
     * ÁîüÊàêÂ∏¶Êó∂Èó¥Êà≥ÁöÑÊñá‰ª∂Âêç
     * @param {string} baseName - Âü∫Á°ÄÊñá‰ª∂Âêç
     * @param {string} extension - Êñá‰ª∂Êâ©Â±ïÂêç
     * @returns {string} Â∏¶Êó∂Èó¥Êà≥ÁöÑÊñá‰ª∂Âêç
     */
    static generateTimestampFilename(baseName, extension) {
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, -5)
        return `${baseName}_${timestamp}.${extension}`
    }

    /**
     * Ëé∑ÂèñÊñá‰ª∂Â§ßÂ∞èÁöÑÂèØËØªÊ†ºÂºè
     * @param {number} bytes - Â≠óËäÇÊï∞
     * @returns {string} Ê†ºÂºèÂåñÁöÑÊñá‰ª∂Â§ßÂ∞è
     */
    static formatFileSize(bytes) {
        if (bytes === 0) return '0 Bytes'
        
        const k = 1024
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
        const i = Math.floor(Math.log(bytes) / Math.log(k))
        
        return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
}

export default DownloadUtil
